.GalleryHeading {
  font-family: "montserrat";
  font-size: 3rem;
  font-weight: 700;
  text-align: left;
  padding: 30px;
  padding-left : 4.7rem;

  color: black;
}

.Gallery {
  width: 100vw;
  overflow: hidden;

  padding-left: 5rem;
  padding-right: 5rem;
}

.GalleryImage {
  padding-top: 20px;
  padding-bottom: 10px;

  width: 95%;
  height: auto;
}

@media (max-width: 991px) {
  .GalleryHeading {
    font-size: 2.5rem;
  }
}

@media (max-width: 576px) {
  .Gallery {
    padding-left: 20px;
    padding-right: 20px;
  }

  .GalleryImage {
    padding: -10px;
  }

  .GalleryHeading {
    font-size: 1.5rem;
    padding-top: 50px;
    padding-bottom: 10px;
  }
}


