/* before AFter  */

.before_after {
  display: flex;
  justify-content:space-evenly;
  align-items: center;

  border: 2px solid rgb(173, 173, 173);
  margin: 0px 100px;
  border-radius: 20px;


}

.before_after div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}



.before_after button {
  margin : 20px 50px;
  padding : 10px 20px;
  border-radius: 0px;
  border: 0px ;
  background-color: transparent;

  color: black;
  font-weight: 500;
  font-family: 'inter';

  font-size: 1.5rem;

}

.before_after div:hover {
  transition: 0.2s;
  background-color: white;
  border: 3px solid black;
  border-radius: 20px;

}

.before_after div:hover button {
  color: black;
}


.before_after_active {
  border-radius: 0px;
  border: 0px ;
  background-color: black;
  color: white !important;

  border-radius: 20px;
}

.before_after_active button {
  transition: 0.2s;
  color: white;
}

.ArrowRightGallery {
  height: 30px;
  width: 30px;
  padding: 2px;
  color: white;
  background-color: white;
  border-radius: 100px;
  border: 0px;
  margin-left: 10px;
}